<template>
    <div class="page">
        <div class="title">
            全部
        </div>
        <div class="filteBar">
            <el-button type="primary" size="medium">添加实验<i class="el-icon-plus el-icon--right"></i></el-button>
        </div>
        <div class="title">
            已有实验
        </div>
        <div v-if="!loading&&experimentList.length==0||!experimentList">
            <el-alert
                title="暂无数据"
                type="info"
                center
                show-icon>
            </el-alert>
        </div>
        <spin :loading="loading"/>
        <div v-for="(item,index) in experimentList" :key="index">
            <div @click="doExperiment(item.experimentId)" class="item">
                <div class="left">
                    <img src="@/assets/image/logo/logo80.png" alt="">
                    <span class="description">
                        <h4>{{item.experimentName}}</h4>
                        <div>{{item.experimentText}}</div>
                        <div>{{item.submitNumber}}人已提交 | 2019-09-14</div>
                    </span>
                </div>
                <div class="right">
                    <div><i class="el-icon-info"></i> 详情</div>
                    <div>{{item.recordStatus | recordStatus}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import spin from '@/components/spin.vue'
export default {
    props: {

    },
    data() {
         return {
             experimentList: [],
             loading: true
         };
     },
     computed: {
 
     },
     created() {
        this.$http.get(`/teaching/student/course/info/${this.$route.params.detailId}`
        ).then((res) => { 
            if(res.data.code == "0"){
                this.experimentList = res.data.data.experimentDTOList
            }else if (res.data.code == "1") {
                this.$router.push('/login'); 
            }else{
                this.$message({
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.loading=false
        })
        .catch(function (error) {
            console.log(error)
        })
     },
    filters: {
      recordStatus: (value) => {
        switch(value){
            case 0:return '待审核';break;
            case 1:return '已审核';break;
            case 2:return '未通过';break;
            case 3:return '未提交';break;
            case 4:return '未解锁';break;
            default:return '';
        }
      },
    },
     mounted() {
 
     },
     watch: {
 
     },
     methods: {
      handleClick(tab, event) {
        switch(tab.label){
            case '实验':this.$router.push(`/courseList/${this.$route.params.detailId}`);break;
            case '资源':this.$router.push(`/courseList/resource/${this.$route.params.detailId}`);break;
            case '课程详情':this.$router.push(`/courseList/detail/${this.$route.params.detailId}`);break;
            default:return;
        }
      },
      doExperiment(id){
          this.$router.push(`/courseList/${this.$route.params.detailId}/${id}`)
      }
     },
    components: {
        spin
    },
};
</script>

<style scoped lang="scss">
.page{
    // height: 500px;
    padding:0 20px;
    .filteBar{
        padding: 1em 0;
    }
    .title{
        padding:10px;
        position: relative;
        border-bottom: 1px solid #E9E9E9;
        font-weight: bold;
    }
    .item{
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #E9E9E9;
        padding:1em;
        .left{
            .description{
                display: inline-block;
                line-height: 1.9em;
                margin-left: 0.3em;
                div{
                    color:#7F7E7E;
                    font-size: 14px;
                }
            }
        }
        .right{
            // display: flex;
            // align-items: flex-end;
            display: inline-block;
            div{
                font-size: 14px;
                color:#7F7E7E;
            }
        }
    }
}
</style>



